import React, { useState } from 'react';

const PreviaCardapio = () => {
  const [pagina, SetPagina] = useState(1);

  const paginaMais = () => {
    if (pagina < 3) {
      SetPagina((pagina) => pagina + 1);
    }
  };
  const paginaMenos = () => {
    if (pagina > 1) {
      SetPagina((pagina) => pagina - 1);
    }
  };

  return (
    <div className="postCardapioEntregas">
      <div className="cont-post-cardapio">
        <div className="titulo-cardapio">Card√°pio</div>
        <div className="cont-cardapio">
          <img
            src={require(`./arquivos/cardapio-${pagina}.jpg`)}
            className="previaCardapio"
            alt="comida"
          ></img>
        </div>

        <div className="troca-pag2">
        <button  onClick={paginaMenos}>&#60;</button>
          {/* &#160; P√°gina {pagina} &#160; */}
          <div className="titulo-trocapag">&#160;P√°gina {pagina} &#160;</div>
          <button  onClick={paginaMais}>&#62;</button>
        </div>

      </div>

      <div className="cont-entregas">
        <div className="titulo-entrega">Entregas</div>
        <div className="entregas"> 
        <div className="previaEntregas">
          <div className="texto-entregas"> Ligue ou pe√ßa pelo whatsapp: 2541-2897 <br/> <br/>  Entregamos sem taxa em Copacabana e Leme. Para Ipanema, Botafogo, Humait√° e Urca a taxa de entrega √© de R$ 6,00</div>
        </div>
        <div className="botaoEntrega"> 
        
        <div className="logo-zap"></div>  
        Whatsapp
        </div>
        </div>
      </div>
    </div>
  );
};

export default PreviaCardapio;
